//-=-=-=-=-=-=-=-=-=-=-IMPORTS
import { db } from '../services/firebase'
import { collection, query, orderBy, onSnapshot, updateDoc, doc } from "firebase/firestore";
import { useEffect, useState, useRef } from 'react';
import Moment from 'react-moment'
import 'moment/locale/pt-br'
import Draggable from 'react-draggable'
import {showMessageMark} from "./SendMessage";
import styles from './Messages.module.css'
//-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-

//Inicio
function Messages(props){
    //algumas variaveis
    const [dataMessages, setDataMessages] = useState([]);
    const [autoScroll, setAutoScroll] = useState(true);
    const renderMessages = [];
    const nodeRef = useRef(null);
    const containerRef = useRef(null);
    const messagesEndRef = useRef(null);

    var dataLast = new Date("2022-04-01");
    var dataNow = new Date();
    dataLast.setHours(0,0,0,0);
    dataNow.setHours(0,0,0,0);
    
    //Verifica se h atulizao no banco de dados
    useEffect(() => {
        const q = query(collection(db, "messages"), orderBy("clientTimestamp", "desc"));
        const unsubscribe = onSnapshot(q, (snapshot) => {
            const updatedDataMessages = snapshot.docs.map((doc) => {
                const data = doc.data();
                data.id = doc.id;
                return data;
            });
            setDataMessages(updatedDataMessages);
        });
        return () => unsubscribe();
    }, []);

    //Rola at em baixo ao abrir a pgina
    useEffect(() => {
        if(autoScroll) containerRef.current.scrollTop = containerRef.current.scrollHeight;
    }, [dataMessages, autoScroll]);
    
    //Funo para verificar o tipo de arquivo
    function isImage(link) {
        const extensions = ['jpg', 'jpeg', 'png', 'gif', 'bmp', 'svg'];
        const urlParts = link.split('.');
        const extension = urlParts[urlParts.length - 1].toLowerCase();
        return extensions.includes(extension);
    }
    function isAudio(link) {
        const extensions = ['mp3', 'ogg'];
        const urlParts = link.split('.');
        const extension = urlParts[urlParts.length - 1].toLowerCase();
        return extensions.includes(extension);
    }

    //Percorre no dataMessages todas as messagens
    dataMessages.forEach( async (item, index) => {
        //Imprimir datas 
        if(index===0) dataLast.setFullYear(item.clientTimestamp.toDate().getFullYear(), item.clientTimestamp.toDate().getMonth(), item.clientTimestamp.toDate().getDate());
        if(dataLast.toLocaleDateString("pt-BR") !== item.clientTimestamp.toDate().toLocaleDateString("pt-BR")){
            const daysDiff = (dataNow.getTime() - dataLast.getTime()) / (1000 * 60 * 60 * 24);
            const dateLabel = daysDiff === 0 ? "Hoje" : daysDiff === 1 ? "Ontem" : <Moment locale='pt-BR' format='LL'>{dataMessages[index-1].clientTimestamp.toDate()}</Moment>;
            renderMessages.push(<p key={`date-${index}`} className={styles.datas}><br/><font color="grey">{dateLabel}</font></p>);
            dataLast.setFullYear(item.clientTimestamp.toDate().getFullYear(), item.clientTimestamp.toDate().getMonth(), item.clientTimestamp.toDate().getDate());
        }
        
        //Arrastar mensagem
        var positionMessage = 0;
        var isMoving = false;
        const handleStop = (event, data) => {
            if(data.lastX >= 100) showMessageMark(dataMessages, (dataMessages.length - 1) - index);
            if(data.lastX >= 1) isMoving = true;
            else isMoving = false;
            positionMessage = 0;
        };

        //rolar at a mensagem marcada
        function scrollToMessage(){
            const boxMessage = document.querySelectorAll(`.${styles.button}`);
            if(item.clientMessageMark!=null&&!isMoving){
                boxMessage[(dataMessages.length - 1) - item.clientMessageMark].scrollIntoView({ behavior: "smooth" });
                boxMessage[(dataMessages.length - 1) - item.clientMessageMark].classList.remove(styles.deslizar);
                setTimeout(() => boxMessage[(dataMessages.length - 1) - item.clientMessageMark].classList.add(styles.deslizar), 5);
                boxMessage[(dataMessages.length - 1) - item.clientMessageMark].addEventListener('animationend', () => {
                    boxMessage[(dataMessages.length - 1) - item.clientMessageMark].classList.remove(styles.deslizar);
                });
            }
        }

        //Pegar o nome do arquivo caso ele exista
        var fileName = null;
        if(item.clientArchive!==null) fileName = item.clientArchive.split('/').pop().split('?')[0];

        //Desenha a mensagem
        renderMessages.push(
            <Draggable axis='x' bounds={{left: 0, right: 100}} onStop={handleStop} position={{x:positionMessage, y: 0}} key={`message-${index}`} nodeRef={nodeRef}>
                <div className={styles.button+ " " + (item.clientName === props.name ? styles.right : styles.left)} ref={nodeRef} onClick={scrollToMessage}>
                    {item.clientMessageMark!==null ? <>{dataMessages[(dataMessages.length - 1) - item.clientMessageMark].clientName}: {dataMessages[(dataMessages.length - 1) - item.clientMessageMark].clientMessage}</>:<></>}
                    <h5>{item.clientName}</h5>
                    {item.clientArchive !== null ? <>{isImage(fileName) ? <img src={item.clientArchive} alt="oi"/> : <>{isAudio(fileName) ? <audio src={item.clientArchive} controls="controls"/> : <a href={item.clientArchive}>{decodeURIComponent(fileName)}</a> }</>}</> : <></>}
                    <h4>{item.clientMessage}</h4>
                    <h6><Moment locale='pt-BR' format='HH:mm'>{item.clientTimestamp.toDate()}</Moment>
                    {item.clientName === props.name ? <font color={item.clientViewed ? "white" : "black"}>v</font> : <></>}</h6>
                </div>
            </Draggable>
        );

        //Data da primeira mensagem
        if(index===dataMessages.length-1){
            const daysDiff = (dataNow.getTime() - dataLast.getTime()) / (1000 * 60 * 60 * 24);
            const dateLabel = daysDiff === 0 ? "Hoje" : daysDiff === 1 ? "Ontem" : <Moment locale='pt-BR' format='LL'>{dataLast}</Moment>;
            renderMessages.push(<p key={`date-${index+1}`} className={styles.datas}><br/><font color="grey">{dateLabel}</font></p>);
        }

        //Visualiza a mensagem
        if(item.clientName!==props.name) await updateDoc(doc(db, "messages", item.id), {
            clientViewed: true
        });

        //Se a mensagem foi enviada pelo client desce at em baixo
        if(index===0){
            if(item.clientName===props.name) toScrollDown();
            else console.log('tem mensagem nova!');
        }
    });

    //Rola at em baixo
    function toScrollDown(){
        messagesEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }

    //Verifica se j foi rolado at em baixo quando abre a pgina
    function handleScroll() {
        const { scrollTop, scrollHeight, clientHeight } = containerRef.current;
        const isAtBottom = scrollTop + clientHeight === scrollHeight;
        setAutoScroll(isAtBottom);
    }

    return (
        <div ref={containerRef} onScroll={handleScroll} className={styles.chat}>
            <div ref={messagesEndRef}/>
            {dataMessages.length > 0 ? renderMessages : <p>A privacidade das mensagens  altamente segura, pois utilizamos o Firebase do Google como banco de dados para armazen-las.</p>}
            <button onClick={toScrollDown} className={styles.buttonDown}></button>
        </div>
    )
}

export default Messages