//-=-=-=-=-=-=-=-=-=-=-IMPORTS
import { db, storage } from '../services/firebase'
import { collection, addDoc} from "firebase/firestore";
import { ref, uploadBytes, getDownloadURL } from "firebase/storage";
import { useRef, useState } from "react";
import RecordRTC from "recordrtc";
import Draggable from 'react-draggable'
import styles from './Messages.module.css'
//-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-

//Abir e fechar a caixa da mensagem marcada
var indexMessageMark = null;
function showMessageMark(dataMessages, value){
    const boxRespond = document.getElementById("boxRespond");
    const client = document.getElementById('client');
    var clientMessage = dataMessages[(dataMessages.length - 1) - value].clientName+': '+dataMessages[(dataMessages.length - 1) - value].clientMessage;
    client.textContent = clientMessage;
    boxRespond.classList.remove(styles.close);
    indexMessageMark = value;
}
function closeMessageMark(){
    const boxRespond = document.getElementById("boxRespond");
    boxRespond.classList.add(styles.close);
    indexMessageMark = null;
}

//Inicio
function SendMessage(props){
    const [inputValue, setInputValue] = useState('');
    const [selectedFile, setSelectedFile] = useState(null);
    const [recorder, setRecorder] = useState(null);
    const nodeRef = useRef(null);

    //Pega o arquivo selecionado
    function handleFileChange(event){
        const sendArchiveCloseButton = document.getElementById("sendArchiveCloseButton");
        sendArchiveCloseButton.classList.remove(styles.close);
        setSelectedFile(event.target.files[0]);
    }

    //Boto para enviar a mensagem
    const handleClick = async () => {
        if(inputValue.trim() !== '' || selectedFile !== null) {
            let imageDownloadURL = null;
            if(selectedFile !== null){
                const fileRef = ref(storage, selectedFile.name);
                await uploadBytes(fileRef, selectedFile).then(async (snapshot) => {
                    console.log('Upload do arquivo concludo com sucesso');
                    imageDownloadURL = await getDownloadURL(fileRef);
                }).catch((error) => {
                    console.log('Ocorreu um erro durante o upload:', error);
                });
            }
            uploadMessage(inputValue, imageDownloadURL);
        }
    };

    //Faz o uploud da mensagem
    async function uploadMessage(message, DownloadURL){
        await addDoc(collection(db, "messages"),{
            clientName: props.name,
            clientMessage: message,
            clientTimestamp: new Date(),
            clientMessageMark: indexMessageMark,
            clientViewed: false,
            clientArchive: DownloadURL
        });

        closeMessageMark();
        sendArchiveClose();
    }

    //boto de fechar o arquivo selecionado
    function sendArchiveClose(){
        const sendArchiveCloseButton = document.getElementById("sendArchiveCloseButton");
        sendArchiveCloseButton.classList.add(styles.close);
        setInputValue('');
        setSelectedFile(null);
        document.getElementById('archives').value = '';
    }

    var positionMessage = 0;
    const handleStop = (event, data) => {
        if(data.lastX<=-100){
            recorder.stopRecording();
            setRecorder(null);
        }else{
            recorder.stopRecording( async () => {
                setRecorder(null);
                let audioDownloadURL = null;
                const fileRef = ref(storage, new Date()+".mp3");
                await uploadBytes(fileRef, recorder.getBlob()).then(async (snapshot) => {
                    console.log('Upload do arquivo concludo com sucesso');
                    audioDownloadURL = await getDownloadURL(fileRef);
                }).catch((error) => {
                    console.log('Ocorreu um erro durante o upload:', error);
                });
                uploadMessage('', audioDownloadURL);
            });
        }
        positionMessage = 0;
    };

    const handleMouseDown = (event) => {
        navigator.mediaDevices.getUserMedia({ audio: true }).then((mediaStream) => {
            const options = {
                type: 'audio',
                mimeType: 'audio/webm',
                bitsPerSecond: 128000
            };
            const record = RecordRTC(mediaStream, options);
            record.startRecording();
            setRecorder(record);
        }).catch((err) => console.error(err));
    };

    return (
        <div className={styles.sendMessageBox}>
            <div id="boxRespond" className={styles.close +" "+ styles.boxRespond}><p id="client"></p><button onClick={closeMessageMark}>x</button></div>
            <div className={styles.boxMessage}>
                <div className={styles.fileInput}>
                    <label for="archives">A</label>
                    <button id="sendArchiveCloseButton" className={styles.close +" "+ styles.tt} onClick={sendArchiveClose}>x</button>
                    <input type="file" id="archives" className={styles.close} onChange={handleFileChange} />
                </div>
                <input type="text" className={styles.boxText} placeholder="Digite uma mensagem..." value={inputValue} onChange={(e) => setInputValue(e.target.value)}/>
                <input type="submit" className={styles.sendButton} onClick={handleClick} value=""/>
                <Draggable axis='x' bounds={{left: -100, right: 0}} position={{x:positionMessage, y: 0}} onMouseDown={handleMouseDown} onStop={handleStop} nodeRef={nodeRef}><button ref={nodeRef} className={styles.sendButton}></button></Draggable>
            </div>
        </div>
    )
}

export {showMessageMark};
export default SendMessage